import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, NavLink, useNavigate } from 'react-router-dom';
import HomePage from './pages/HomePage';
import ButtonsPage from './pages/ButtonsPage';
import FormsPage from './pages/FormsPage';
import AlertsPage from './pages/AlertsPage';
import PlaygroundPage from './pages/PlaygroundPage';

// Redirect handler component
function RedirectHandler() {
  const navigate = useNavigate();
  
  useEffect(() => {
    const redirectPath = sessionStorage.getItem('redirectPath');
    if (redirectPath) {
      sessionStorage.removeItem('redirectPath');
      navigate(redirectPath, { replace: true });
    }
  }, [navigate]);
  
  return null;
}

function App() {
  const navigation = [
    { path: '/', label: '概要', icon: '🏠' },
    { path: '/buttons', label: 'Buttons', icon: '🔘' },
    { path: '/forms', label: 'Forms', icon: '📝' },
    { path: '/alerts', label: 'Alerts', icon: '⚠️' },
    { path: '/playground', label: 'Playground', icon: '🎮' }
  ];

  return (
    <Router>
      <div className="app">
        <nav className="nav-header">
          <div className="nav-container">
            <NavLink to="/" className="nav-logo">
              UI Components
            </NavLink>
            <ul className="nav-links">
              {navigation.map(item => (
                <li key={item.path}>
                  <NavLink
                    to={item.path}
                    className={({ isActive }) => 
                      `nav-link ${isActive ? 'active' : ''}`
                    }
                    end={item.path === '/'}
                  >
                    {item.icon} {item.label}
                  </NavLink>
                </li>
              ))}
            </ul>
          </div>
        </nav>

        <main className="main-content">
          <RedirectHandler />
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/buttons" element={<ButtonsPage />} />
            <Route path="/forms" element={<FormsPage />} />
            <Route path="/alerts" element={<AlertsPage />} />
            <Route path="/playground" element={<PlaygroundPage />} />
          </Routes>
        </main>

        <footer style={{ 
          textAlign: 'center', 
          padding: 'var(--spacing-8)', 
          color: 'var(--color-neutral-600)',
          borderTop: '1px solid var(--color-neutral-200)',
          backgroundColor: 'var(--color-neutral-white)'
        }}>
          <p>UI Components Library - Laravel + React + Inertia.js</p>
          <p style={{ fontSize: 'var(--font-size-sm)', marginTop: 'var(--spacing-2)' }}>
            Navy Theme Design System
          </p>
          <div style={{ marginTop: 'var(--spacing-3)', fontSize: 'var(--font-size-sm)' }}>
            <span>🔗 Direct Links: </span>
            {navigation.map((item, index) => (
              <React.Fragment key={item.path}>
                <NavLink 
                  to={item.path}
                  style={{ 
                    color: 'var(--color-primary-600)', 
                    textDecoration: 'none',
                    fontWeight: 'var(--font-weight-medium)'
                  }}
                >
                  {item.label}
                </NavLink>
                {index < navigation.length - 1 && <span style={{ margin: '0 var(--spacing-2)' }}>•</span>}
              </React.Fragment>
            ))}
          </div>
        </footer>
      </div>
    </Router>
  );
}

export default App;